@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
html{font-size:14px}body{font-family:'Ubuntu', sans-serif}a,a:hover{text-decoration:none}@media (max-width: 991px){.container{padding-left:20px;padding-right:20px;min-width:100%}}.sidebar{position:fixed;top:0;bottom:0;left:0;min-height:100vh !important;z-index:100;padding:48px 0 0;padding-left:10rem;box-shadow:inset -1px 0 0 rgba(0,0,0,0.1);text-align:center;background-color:#bd5b3c}.sidebar .imageRound{border-radius:10rem}.sidebar .sidebar-link{font-size:22px;font-size:1.57143rem;font-weight:bold;line-height:normal;color:#fff;position:relative}.sidebar .sidebar-link:after{content:"";position:absolute;width:100%;height:1px;bottom:-1px;left:0;background-color:#fff;visibility:hidden;transform:scaleX(0);transition:all 0.3s ease-in-out 0s}.sidebar .sidebar-link:hover:after,.sidebar .sidebar-link.active:after{visibility:visible;transform:scaleX(1)}@media (max-width: 991px){.sidebar .sidebar-link{padding:15px 0;border-bottom:1px solid #19212d;font-size:18px;font-size:1.28571rem;font-weight:300}.sidebar .sidebar-link:hover,.sidebar .sidebar-link.active{color:#fff}}@media (min-width: 992px){.sidebar .sidebar-link{margin:0 25px}.sidebar .sidebar-link:after{height:3px;bottom:-3px}}.nav-item{padding-top:10px}#sidebar-wrapper{min-height:100vh !important;width:100vw;margin-left:-1rem;transition:margin 0.25s ease-out}#sidebar-wrapper .sidebar-heading{padding:0.875rem 1.25rem;font-size:1.2rem}#page-content-wrapper{min-width:0;width:100%}section{background-color:#fff;height:100vh;color:#fa3840;text-align:center;display:flex;align-items:center;justify-content:center}

